import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { WifiCode } from '../wifi-code';
import { WizardService, IDictionary } from '../wizard.service';

@Component({
  selector: 'app-wifi-form',
  templateUrl: './wifi-form.component.html',
  styleUrls: ['./wifi-form.component.css']
})
export class WifiFormComponent {

  model = new WifiCode('', 'WPA', '', false, false);
  show: boolean;
  showkey: boolean;
  qrflag: boolean;
  keyErrorMessage: string;
  QrForm: FormGroup;

  constructor(public wizardService: WizardService) {
    this.QrForm = new FormGroup({
      ssid: new FormControl(this.model.ssid, Validators.required),
      encryption: new FormControl(this.model.encryption),
      nwkey: new FormControl(this.model.nwkey, this.keyValidator.bind(this)),
      hidden: new FormControl(this.model.hidden),
      encryptQr: new FormControl(this.model.encryptQr),
    });
    this.keyErrorMessage = 'Password is required';
    this.wizardService.name = 'Smith!';
    this.onChanges();
    this.show = false;
    this.showkey = true;
    this.wizardService.qrValid = this.QrForm.valid;
  }

  onChanges(): void {
    this.QrForm.valueChanges.subscribe(val => {
      this.showHideKeyField();
      this.updateKeyError();
      this.wizardService.jsonData = this.QrForm.value;
      this.wizardService.encodeJson.emit();
      this.wizardService.qrValid = this.QrForm.valid;
    });
  }

  togglePw() {
    this.show = !this.show;
    // console.log('Key is:', this.QrForm.value.nwkey.toString());
  }

  showHideKeyField() {
    if (this.QrForm.value.encryption.toString() === 'none') {
      this.QrForm.value.nwkey = '';
      this.showkey = false;
    } else {
      this.showkey = true;
    }
  }

  keyValidator = function(control: FormControl) {
    if (control.value !== '' && this.showkey) {
      let pwsize = control.value.length;
      if (this.QrForm.value.encryption.toString() === 'WEP') {
        if (pwsize === 5 || pwsize === 13) {
          return null;
        } else {
          return {
            errorObj: {
              someobj: 'error'
            }
          };
        }
      }
    }
    return null;
  };

  updateKeyError() {
    if (this.QrForm.value.encryption.toString() === 'WEP') {
      this.keyErrorMessage = 'WEP Key must be either 5 or 13 characters';
    } else {
      this.keyErrorMessage = 'Password is required';
    }
  }
}


